<template>
  <div style="background:#f1eff2;height:100%;">
    <mu-appbar style="width: 100%;" color="#4ed995" class="header">
      <a href="#/Map">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span>监控车数据</span>
    </mu-appbar>
    <div style="background:#4ed995;height:200px;width:100%;"></div>
    <div class="MapDetailscontainer">
      <div class="MapDetailsOneContent" v-for="(item,index) in contentList" :key="index">
        <div class="mapDetailsTitle">
          <span style="color:#080307;font-weight:600;font-size:14px;">数据列表</span>
        </div>
        <div class="mapDetailscontentList">
          <span>pH</span>
          <span>{{item.ph}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>COD浓度/mg/L</span>
          <span>{{item.cod}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>总氨氮浓度/mg/L</span>
          <span>{{item.ammoniaNitrogen}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>总氮浓度/mg/L</span>
          <span>{{item.nitrogen}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>总铜浓度/mg/L</span>
          <span>{{item.cuprum}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>总镍浓度/mg/L</span>
          <span>{{item.nickel}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>总铬浓度/mg/L</span>
          <span>{{item.chromium}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>总氰化物浓度/mg/L</span>
          <span>{{item.prussiate}}</span>
        </div>
        <div class="mapDetailscontentList">
          <span>总磷浓度/mg/L</span>
          <span>{{item.phosphorus}}</span>
        </div>
      </div>
      <div class="MapDetailsTwoContent">
        <div class="mapDetailsTitle">
          <span style="color:#080307;font-weight:600;font-size:14px;">相关站点</span>
        </div>
        <div class="mapDetailsTwoList">
          <div class="mapDetailsImage">
            <img src="../../../static/img/pointRed.png" alt />
          </div>
          <span>沙井一期进水口水质监测站</span>
        </div>
        <div class="mapDetailsTwoList">
          <div class="mapDetailsImage">
            <img src="../../../static/img/pointOrange.png" alt />
          </div>
          <span>沙井一期进水口水质监测站</span>
        </div>
        <div class="mapDetailsTwoList">
          <div class="mapDetailsImage">
            <img src="../../../static/img/pointOrange.png" alt />
          </div>
          <span>沙井一期进水口水质监测站</span>
        </div>
      </div>
      <!-- <div class="MapDetailsThreeContent">
        <div class="mapDetailsTitle">
          <span style="color:#080307;font-weight:600;font-size:14px;">现场图片</span>
        </div>
        <div class="imageListCcontentThree">
          <div v-for="(item,index) in imageList" :key="index" class="imageList">
            <img :src="url+item" alt />
          </div>
        </div>
      </div> -->
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      msg: "hello 数据详情",
      imageList: [],
      contentList: [],
      url: "http://47.112.16.168:",
      photo:""
    };
  },
  methods: {
    async initData(e,f) {
      console.log("xie");
      var url = "/mobile/routing";
      var pageNum = 1;
      var pageSize = 10000000;
      var startTime = "";
      var endTime = "";
      var vehicleId = "";
      var res = await AppBace.request(
        url,
        "GET",
        {
          pageNum,
          pageSize
        },
        this.token
      );
      console.log("xie", res);
      var array = [];
      array = res.data.data.rows;
      for (let index = 0; index < array.length; index++) {
        
        var  name = array[index].routingName;
        
        
        if (name === e) {
          var arr= [];
            // arr.push(array[index]);
            arr = array[index].monitoringDataList;
            this.photo = arr[f].photo;
           this.contentList.push(arr[f]);
          //  debugger
           console.log(this.contentList);
           var strPhoto = this.photo;
           
           this.imageList = strPhoto.split(",");
          //  for (let i = 0; i < this.contentList.length; i++) {
          //    const id = this.contentList[i];
             
          //  }
        } else {
        }
        // array.push();
      }

     
    }
  },
 async mounted() {
  
    var name = this.$route.query.routingName;
    var id = this.$route.query.id;
    this.token = sessionStorage.getItem("token");
    await this.initData(name,id);
  },
  destroyed() {
    console.log(1);
  }
};
</script>
<style scoped>
.MapDetailscontainer {
  position: absolute;
  width: 100%;
  top: 50px;
  padding: 0 6px;
  /* margin: 0 5px; */
}
.MapDetailsOneContent {
  width: 100%;
  padding: 10px 40px;
  border-radius: 10px;

  /* margin:0 5px; */
  background: white;
}
.mapDetailsTitle {
  border-bottom: 1px rgb(241, 240, 240) solid;
  width: 100%;
  height: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  text-align: center;
}
.mapDetailscontentList {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 40px;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  border-bottom: rgb(241, 240, 240) 1px solid;
}
.mapDetailsImage img {
  width: 12px;
  height: 12px;
}
.mapDetailsTwoList {
  display: flex;
  flex-direction: row;
  height: 30px;
  align-items: center;
}
.MapDetailsTwoContent {
  background: white;
  margin-top: 8px;
  border-radius: 10px;
  padding: 10px 40px;
}
.MapDetailsThreeContent {
  background: white;
  margin-top: 10px;
  border-radius: 10px;
  padding: 0 10px;
}
.imageList img {
  width: 100%;
  height: auto;
  padding: 5px;
}
.imageListCcontentThree {
  padding: 0 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
</style>



